import React from 'react';
import moment from 'moment-timezone'
import { useState } from 'react';
import { useRef } from 'react';
import "moment/locale/ko"

const Moment = () => {
    const birtDayRef = useRef(null);
    const [days , setDay] = useState("");
    const handleBirthDayChange = (event) => {
        setDay(moment(event.target.value , "YYYY-MM-DD").format("dddd"))
    }

    console.log(days)

    const momentDate = moment();
    const newMomentDate = momentDate.add(1, "week");
    const cloneNewMomentDate = newMomentDate.clone().add(1,"week");

    return (
        <div>
            <h1>Moments</h1>
            <div>Immutable Check</div>
            <div>
                {momentDate.format()}
                <br />
                {newMomentDate.format()}
                <br />
                {cloneNewMomentDate.format()}
            </div>
            <br />
            <div>Summer Time New-york</div>
            <div>
                2018 3 10 13   : 
                {moment.tz("2018-03-10 13:00:00","America/New_York").add(1,"day").format( )}
            </div>
            <div>
                2018 3 10 13 24  : {moment.tz("2018-03-10 13:00:00","America/New_York").add(24,"hour").format( )}
            </div>
            <br />
            <div>Leap year()</div>
            <div>
                2017 1 1 1  : 
                {moment("2017-01-01 13:00:00").add(-1,"year").format()}
            </div>
            <div>
                2017 1 1 365   :
                {moment("2017-01-01 13:00:00").add(365,"day").format()}
            </div>

            <br />
            <div> </div>
            <div>
                {moment("2017-01-01 13:00:00").format("YYYY M D")}
            </div>

            <br />
            <div>  </div>
            <div>
                <input type="date" ref={birtDayRef} onChange={handleBirthDayChange}/>
                <div> </div>
                <div>
                    {days}
                </div>
            </div>
            <br />
            <div>  </div>
            <div>2021-07-17 03:00  2021-07-18 02:00  </div>
            <div>
                {moment("2021-07-11 03:00").diff("2021-07-18 02:00",'hour')}
            </div>
        </div>
    );
};

export default Moment;